body {
  background-image: url(https://image.shutterstock.com/image-photo/abstract-blurred-image-interior-shopping-600w-572755054.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
}

.App {
  display: flex;
}

.initial-button {
  width: 120px;
  height: 40px;
  background-color: aqua;
  font-size: 24px;
  border-radius: 10px;
}

.products {
  display: flex;
  flex-direction: column;

  &__title {
    font-size: 24px;
  }

  &__list {
    list-style: none;
    font-size: 20px;
  }
}

.button {
  margin: 0px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;

  &__choose {
    width: 140px;
    margin-bottom: 20px;
  }
}

.label {
  display: flex;
  align-items: center;
  margin-right: 40px;
}

